@import url(https://cdn.jsdelivr.net/npm/boxicons/css/boxicons.min.css);
@import url(https://fonts.googleapis.com/css2?family=Montserrat&display=swap);
:root {
	--toolbar-width: 50px;
	--toolbar-padding: 10px;
	--toolbar-text: none;
	--background-color: #fff;
	--shadow-color: #cbcbcb;
	--nav-color: transparent;
	--text-color: #000;
	--text-color-light: #fff;
	--nav-time: 0.1s;
}

body {
	margin: 0;
	background: #fff;
	background: var(--background-color);
	color: #000;
	color: var(--text-color);
	font-family: "Montserrat", sans-serif;
}

.center {
	display: flex;
	align-items: center;
	justify-content: center;
	text-align: center;
}

.content {
	padding-left: calc(10px + 50px);
	padding-left: calc(var(--toolbar-padding) + var(--toolbar-width));
}

.fullpage {
	height: 100vh;
}

.gradient {
	background: linear-gradient(315deg, #ee7752, #e73c7e, #23a6d5, #23d5ab);
	background-size: 800% 800%;
	-webkit-animation: GradientAnimation 20s ease infinite;
	        animation: GradientAnimation 20s ease infinite;
}

@-webkit-keyframes GradientAnimation {
	0% {
		background-position: 0% 51%;
	}
	50% {
		background-position: 100% 50%;
	}
	100% {
		background-position: 0% 51%;
	}
}

@keyframes GradientAnimation {
	0% {
		background-position: 0% 51%;
	}
	50% {
		background-position: 100% 50%;
	}
	100% {
		background-position: 0% 51%;
	}
}

@media screen and (max-width: 600px) {
	.content {
		padding-left: calc(10px + 50px);
		padding-left: calc(var(--toolbar-padding) + 50px);
	}
	nav {
		background: transparent;
		background: var(--nav-color);
	}
}

nav {
	position: fixed;
	top: 0;
	left: 0;
	height: 100%;
	padding: 0px;
	width: var(--toolbar-width);
	border-left: var(--toolbar-padding) #11101d solid;
}

nav:nth-child(1) {
	height: calc(100% - 28px);
	width: var(--toolbar-width);
	padding: 14px 0;
}

nav > ul {
	padding: 0px;
	margin: 0px;
}

.active_cover .child::before {
	content: "";
	position: absolute;
	top: 0;
	left: 0;
	border-radius: -12px 0 0 0;
	width: 12px;
	height: 12px;
	background-image: radial-gradient(circle at 99% 99%, transparent 0px, transparent 11.7px, #11101d 12px);
}

.active_cover .child::after {
	content: "";
	background: #11101d;
	position: absolute;
	top: 100%;
	width: 100%;
	height: 14px;
	border-radius: 0 12px 0 0;
}

.active_cover .child2::after {
	content: "";
	position: absolute;
	bottom: 12px;
	left: 0;
	border-radius: -12px 0 0 0;
	width: 12px;
	height: 12px;
	background-image: radial-gradient(circle at 99% 1%, transparent 0px, transparent 11.7px, #11101d 12px);
}

.active_cover .child2::before {
	content: "";
	background: #11101d;
	position: absolute;
	top: -2px;
	width: 100%;
	height: 14px;
	border-radius: 0 0 12px 0;
}

.active_cover {
	height: 50px;
	width: 100%;
	position: relative;
	top: 2px;
	padding: 12px 0;
}

.active_cover .child {
	width: 100%;
	height: 100%;
	position: relative;
}

.cover {
	position: absolute;
	top: 0;
	bottom: 0;
	left: 0;
	right: 0;
}

#top_fill,
#bottom_fill {
	width: 100%;
	position: absolute;
	background: #11101d;
}

#top_fill {
	top: 0;
	bottom: 974px;
}

#bottom_fill {
	bottom: 0;
	top: 74px;
}

#collapse {
	position: absolute;
	width: 50px;
	height: 50px;
	background-color: #23d5ab;
	padding: 0;
	font-size: x-large;
	border: none;
	bottom: var(--toolbar-padding);
	right: 0;
	border-radius: 12px 0 0 12px;
	padding-left: 2px;
}

#copyright {
	position: absolute;
	text-align: center;
	display: var(--toolbar-text);
	width: 200px;
	border: none;
	bottom: calc(var(--toolbar-padding) + 5px);
	left: 50px;
	font-size: x-small;
	color: var(--text-color-light);
}

#adjust {
	position: absolute;
	display: var(--toolbar-text);
	width: 50px;
	height: 50px;
	background-color: hsl(200, 6%, 30%);
	border: none;
	bottom: calc(var(--toolbar-padding));
	left: 0;
	font-size: large;
	border-radius: 12px;
}

#adjust > i {
	display: flex;
	align-items: center;
	text-decoration: none;
	font-size: x-large;
	justify-content: center;
	height: 100%;
}

.navItem li {
	position: relative;
	height: 50px;
	width: 100%;
	list-style: none;
	line-height: 50px;
	border-radius: 12px;
}

.navItem li a {
	color: var(--text-color-light);
	display: flex;
	align-items: center;
	text-decoration: none;
	justify-content: center;
	cursor: pointer;
	height: 100%;
}

.navItem li a i {
	width: 50px;
	font-size: x-large;
	text-align: center;
}

.navItem li a span {
	flex-grow: 1;
	display: var(--toolbar-text);
}

.navItem li.active a {
	color: var(--text-color);
	background: transparent;
	border-radius: 12px 0px 0px 12px;
}

.active li a {
	color: var(--text-color) !important;
}

nav,
.cover * {
	transition: all var(--nav-time) linear;
}

